import React, { useState } from "react";
import "./counter.css";

function Counter(props){
    let [count] = useState(0);
    let handleMinus = () => {
        count(count => count - 1)
    }
    let handlePlus = () => {
        count(count => count + 1)
    }
    return(
        <div>
            <button onClick={handleMinus}>-</button>
            <p>{count}</p>
            <button onClick={handlePlus}>+</button>
        </div>
    )
}


export default Counter;